import {useUrlQueryParam,useSetUrlSearchParam} from "../../utils/url";
import {useMemo, useState} from "react";
import {useProject} from "../../utils/project";

// 项目列表搜索的参数
export const useProjectsSearchParams = () => {
    const [param, setParam] = useUrlQueryParam(["name", "personId"]);
    return [
        useMemo(
            () => ({ ...param, personId: Number(param.personId) || undefined }),
            [param]
        ),
        setParam,
    ] as const;
};

export const useProjectModal = () => {
      const [{projectCreate}, setProjectCreate] = useUrlQueryParam(['projectCreate']);
      const [{editingProjectId},setEditingProjectId] = useUrlQueryParam(['editingProjectId']);
      const {data:editingProject,isLoading} = useProject(Number(editingProjectId));
      const open = () => setProjectCreate({ projectCreate: true });
      const setUrlParams = useSetUrlSearchParam();
      const close =  () => {
          // return setEditingProjectId({editingProjectId:undefined});
          // return setProjectCreate({projectCreate: undefined});
          return setUrlParams({ projectCreate: "", editingProjectId: "" });
      }
      const startEdit = (id:number) => setEditingProjectId({editingProjectId:id});
      console.log('--------------');
      console.log(editingProject);
      return {
            open,
            close,
            projectModalOpen:projectCreate == 'true' || !!editingProject,
            startEdit,
            editingProject,
            isLoading
      }
}

export const useTextShow = () =>{
      // const [state,setState] = useState(false);
      // const show = () => {
      //     setState(true);
      //     console.log('use textshow true');
      // };
      // const hide = () => setState(false);
      // return {
      //       state,
      //       show,
      //       hide
      // }

    const [{showText}, setShowText] = useUrlQueryParam(['showText']);
    const show = () => setShowText({ showText: true });
    const hide =  () => setShowText({showText: false});

    return {
        show,
        hide,
        state:showText === 'true'
    }
}
