import {Kanban} from "../../types/kanban";
import {useTasks} from "../../utils/task";
import {useTasksSearchParams} from "./util";

import taskIcon from '../../assets/task.svg';
import bugIcon from '../../assets/bug.svg'
import {useTaskTypes} from "../../utils/task-type";
import styled from "@emotion/styled";
import {Button, Card, Dropdown, Menu, Modal} from "antd";
import {CreateTask} from "./create-task";
import {Mark} from '../../components/mark'
import {useDeleteKanban, useDeleteTask} from "../../utils/kanban";
import {Row} from "../../components/lib";

const TaskTypeIcon = ({id}:{id:number}) => {
     const {data:taskTypes} = useTaskTypes()
     const name = taskTypes?.find(taskType => taskType.id === id)?.name;
     if(!name){
          return null;
     }
     return <img src={name === 'task' ? taskIcon :bugIcon}/>
}

export const KanbanColumn = ({kanban}:{kanban:Kanban}) => {
      const {data:allTasks} = useTasks(useTasksSearchParams());
      // const {data:allTasks} = useTasks();
      const tasks = allTasks?.filter(task => task.kanbanId === kanban.id);
      const {name:keyword} = useTasksSearchParams();

      const {mutate:deleteTask} = useDeleteTask();
      const startDelete = (id:number) => {
            Modal.confirm({
                 okText:'确定',
                 cancelText:'取消',
                 title:'确定删除任务吗',
                 onOk(){
                       return deleteTask({id});
                 }

            })
      }
      return <Container>
          <Row between={true}>
              <h3>{kanban.name}</h3>
              <More kanban={kanban}></More>
          </Row>

          {
                tasks?.map(task => <div key={task.id}>
                      <Card>
                          {/*{task.name}*/}
                          <Mark keyword={keyword} name={task.name}></Mark>  <span onClick={() => startDelete(task.id)} style={{color:"red",float:'right',cursor:"pointer"}}>删除</span>
                          <br/>
                      <TaskTypeIcon id={task.typeId}></TaskTypeIcon>
                      </Card>

                </div>)
          }
          <CreateTask kanbanId={kanban.id}/>
      </Container>
}

const More = ({kanban}:{kanban:Kanban}) => {
      const {mutateAsync:deleteKanban}  = useDeleteKanban();
      const startEdit = () => {
            Modal.confirm({
                  okText:'确定',
                  cancelText:'取消',
                  title:'确定删除看板吗',
                  onOk(){
                       return  deleteKanban({id:kanban.id});
                  }
            })
      }
      const overlay = <Menu>
            <Menu.Item>
                   <Button type={'link'} onClick={startEdit}>
                         删除
                   </Button>
            </Menu.Item>
      </Menu>
      return <Dropdown overlay={overlay}>
           <Button type={'link'}>...</Button>
      </Dropdown>

}

export const Container = styled.div`
      min-width: 35rem;
      border-radius: 6px;
      background-color: rgb(244,245,247);
      display: flex;
      flex-direction: column;
      padding: 0.7rem 0.7rem 1rem ;
      margin-right: 1.5rem;
      overflow-y: auto;

`

const TaskContainer = styled.div`
  overflow: scroll;
  flex: 1;
  ::-webkit-scrollbar{
    display: none;
  }

`