import {Form, Input, Select} from "antd";
import {User} from "../../types/user";

interface SearchPanelProps {
     users: User[],
     param: {
           name: string,
         personId: string
     },
     setParam: (param: SearchPanelProps['param']) => void;
}

export const SearchPanel = ({users,param,setParam}: SearchPanelProps) => {
      return <Form layout={"inline"} style={{marginBottom:'2rem'}}>
      `   <Form.Item>
            <Input
                placeholder={'项目名'}
                type="text"
                value={param.name}
                onChange={evt=> setParam({
                ...param,
                name:evt.target.value
            })}/>

          </Form.Item>`
          <Form.Item>
              <Select value={param.personId} style={{width:'150px'}} onChange={value=> {
                  setParam({
                      ...param,
                      personId: value
                  })
              }}>
                  <Select.Option value={''}>负责人</Select.Option>
                  {
                      users.map(user => {
                          return <Select.Option key={user.id} value={String(user.id)}>{user.name}</Select.Option>
                      })
                  }
              </Select>
          </Form.Item>
    </Form>

      // return <form>
      //        <div>
      //            <input types="text" value={param.name} onChange={evt=> setParam({
      //                    ...param,
      //                    name:evt.target.value
      //            })}/>
      //            <select value={param.personId} onChange={evt=> {
      //                 setParam({
      //                      ...param,
      //                     personId: evt.target.value
      //                 })
      //            }}>
      //               <option value={''}>负责人</option>
      //                {
      //                     users.map(user => {
      //                          return <option key={user.id} value={user.id}>{user.name}</option>
      //                     })
      //                }
      //            </select>
      //        </div>
      // </form>
}