import {Project} from "../types/project";
import {useHttp} from "./http";
import {useMutation, useQuery, useQueryClient} from "react-query";
import {Kanban} from "../types/kanban";
import {useAsync} from "./use-async";
import {Task} from "../types/task";

export const useKanbans = (param?: Partial<Kanban>) => {
    const client = useHttp();

    return useQuery<Kanban[]>(["kanbans", param], () =>
        client("kanbans", { data: param })
    );
};

export const useAddKanban = () => {
    const client = useHttp();
    const queryClient  = useQueryClient();
    return useMutation(
        (params:Partial<Kanban>) => client(`kanbans`,{
            data:params,
            method:'POST'
        }),{
            onSuccess:() => queryClient.invalidateQueries('kanbans')
        }
    )
}

export const useAddTask = () => {
    const client = useHttp();
    const queryClient  = useQueryClient();
    return useMutation(
        (params:Partial<Task>) => client(`tasks`,{
            data:params,
            method:'POST'
        }),{
            onSuccess:() => queryClient.invalidateQueries('tasks')
        }
    )
}

export const useDeleteKanban = () => {
    const client = useHttp();
    const queryClient  = useQueryClient();
    return useMutation(
        (params:Partial<Kanban>) => client(`kanbans/${params.id}`,{
            // data:params,
            method:'DELETE'
        }),{
            onSuccess:() => queryClient.invalidateQueries('kanbans')
        }
    )
}
export const useDeleteTask = () => {
    const client = useHttp();
    const queryClient  = useQueryClient();
    return useMutation(
        (params:Partial<Kanban>) => client(`tasks/${params.id}`,{
            // data:params,
            method:'DELETE'
        }),{
            onSuccess:() => queryClient.invalidateQueries('tasks')
        }
    )
}