import {useState} from "react";
import {LoginScreen} from "./login";
import {RegisterScreen} from "./register";
import {Button, Card, Divider,Image, Typography} from "antd";

import styled from  "@emotion/styled"

import logo from '../assets/logo.svg';
import left from '../assets/left.svg';
import right from '../assets/right.svg';
import avator from '../assets/pic.jpg';
import {useDocumentTitle} from "../utils";

const ava = require('../assets/pic.jpg')
export  const UnauthenticatedApp = () => {
      useDocumentTitle('请登录或注册以继续',false);
      const [isRegister, setIsRegister] = useState(true);
      const [error, setError] = useState<Error | null>(null);
      return <Container>
          <Header></Header>
          <Background></Background>
          {/*<img*/}

          {/*    // src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"*/}
          {/*    src={ava.default}*/}
          {/*/>*/}
          <ShadowCard>
              <Title>
                  {
                      isRegister? '请登录' : '请注册'
                  }
              </Title>
              {error?<Typography.Text type={"danger"}>{error?.message}</Typography.Text>:null}
              {
                  isRegister? <LoginScreen onError={setError}></LoginScreen>:<RegisterScreen onError={setError}></RegisterScreen>
              }
              <Divider></Divider>
              <a onClick={()=>setIsRegister(!isRegister)}>{isRegister? '没有账号？注册新账号' : '已经有账号了？直接登录'}</a>
          </ShadowCard>
      </Container>
}
export const LongButton =styled(Button)`
    width:100%;
`
const Title = styled.h2`
   margin-bottom: 2.4rem;
   color:rgb(94,108,132);

`

const Background = styled.div`
   position: absolute;
   width:100%;
   height:100%;
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: left bottom, right bottom;
   background-size: calc(((100vw - 40rem)/2) - 3.2rem),calc(((100vw - 40rem)/2) - 3.2rem);
   background-image: url(${left}),url(${right});
 `
const  Header = styled.header`
    background: url(${logo}) no-repeat center;
    padding: 5rem 0;
    background-size: 8rem;
    width: 100%;
`
const ShadowCard = styled(Card)`
   width:40rem;
   min-height: 56rem;
   padding: 3.2rem 4rem;
   border-radius: 0.3rem;
   box-sizing: border-box;
   box-shadow: rgba(0,0,0,0.1) 0 0 10px;
   text-align: center;
`
const Container = styled.div`
    display:flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
 `