import {Row, ScreenContainer} from "../../components/lib";
import {useProjectInUrl} from "../kanban/util";
import {useDeleteEpic, useEpics} from "../../utils/epic";
import {useEpicSearchParams} from "./util";
import {Button, List} from "antd";
import {useTasks} from "../../utils/task";
import dayjs from "dayjs";
import {Link} from "react-router-dom";

export const EpicScreen = () => {
    const {data:epics} = useEpics(useEpicSearchParams())
    const {data:currentProject} = useProjectInUrl();
    const {data:tasks} = useTasks({projectId:currentProject?Number(currentProject.id):undefined});
    const {mutate:deleteEpic} = useDeleteEpic();
    return <ScreenContainer>
        <h1>{currentProject?.name}任务组</h1>
        <List dataSource={epics} itemLayout={'vertical'} renderItem={epic=> <List.Item>
                <List.Item.Meta   title={<Row between={true}>
                    <span>{epic.name}</span>
                    <Button type={'link'} onClick={e => deleteEpic({id:epic.id})}>删除</Button>
                </Row>}
                 description={<div>
                        <div>开始时间：{dayjs(epic.start).format('YYYY-MM-DD')}</div>
                        <div>结束时间：{dayjs(epic.end).format('YYYY-MM-DD')}</div>
                 </div>}>
                </List.Item.Meta>
                 <div>
                     {tasks?.filter(task => task.epicId === epic.id).map(task => {
                         return <Link key={task.id} to={`/projects/${currentProject?.id}/kanban?`}>
                             {task.name}
                         </Link>
                     })}
                 </div>
        </List.Item>}></List>

    </ScreenContainer>
}