import React from "react";
import {Dropdown, Menu, Modal, Table} from "antd";
import dayjs from "dayjs";
import {TableProps} from "antd/es/table";
import {Link} from 'react-router-dom';
import {Pin} from "../../components/pin";
import {useDeleteProject, useEditProject} from "../../utils/project";
import {ButtonNoPadding} from "../../components/lib";
import {useProjectModal} from "./util";
import {Project} from "../../types/project";
import {User} from "../../types/user";
// interface ListProps {
//       list: Project[],
//       users: User[]
// }

interface ListProps extends TableProps<Project> {
    users: User[];
    setProjectModelOpen:(flag:boolean)=>void
}

export const List = ({users,...props}: ListProps) => {
      const {mutate} = useEditProject();
      // const pinProject = (id:string,pin:boolean) => mutate({id,pin});

      //柯里化
      // const  pinProject = (id:string) => (pin:boolean) =>  mutate({id,pin}).then(retry)
      const  pinProject = (id:string) => (pin:boolean) =>  mutate({id,pin})

      const {startEdit} =useProjectModal();
      const editProjectId = (id:number) => () =>startEdit(id);
        const { mutate: deleteProject } = useDeleteProject();
        const confirmDeleteProject = (id: string) => {
            Modal.confirm({
                title: "确定删除这个项目吗?",
                content: "点击确定删除",
                okText: "确定",
                onOk() {
                    deleteProject({ id });
                },
            });
        };
      return <Table pagination={false}
      columns={[
          {
               title:<Pin checked={true} disabled={true}/>,
               render(value,project){
                     // return <Pin checked={project.pin} onCheckedChange={pin =>pinProject(project.id,pin)}></Pin>
                   return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)}></Pin>
               }
          },

          {
             title:'名称',
             render(value,project){
                 return <Link to={String(project.id)}>{project.name}</Link>
             }
           },
          {
              title:'部门',
              dataIndex:'organization'
          },
          {
            title:'负责人',
            render(value,project){
                 return <span>
                         {users.find(user=> user.id===project.personId)?.name||'未知'}
                 </span>
            }
      },  {
              title:'创建时间',
              render(value,project){
                    return <span>
                        {project.created ? dayjs(project.created).format('YYYY-MM-DD'):'无'}
                    </span>
              }
          },{
              render(value,project){
                  return <Dropdown overlay={<Menu>
                           <Menu.Item key={'edit'}>
                               <ButtonNoPadding type={"link"} onClick={editProjectId(Number(project.id))}>编辑</ButtonNoPadding>
                           </Menu.Item>
                          <Menu.Item key={'delete'}>
                              <ButtonNoPadding type={"link"}  onClick={() => confirmDeleteProject(project.id)}>删除</ButtonNoPadding>
                          </Menu.Item>

                  </Menu>}>
                       <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
                    </Dropdown>
              }
          }]}  {...props}>

      </Table>
      // return  <table>
      //         <thead>
      //             <tr>
      //                     <th>名称</th>
      //                     <th>负责人</th>
      //             </tr>
      //         </thead>
      //        <tbody>
      //        {
      //                list.map(project => {
      //                      return (
      //                            <tr key={project.id}>
      //                                  <td>{project.name}</td>
      //                                  <td>{users.find(user=> user.id===project.personId)?.name||'未知'}</td>
      //                            </tr>
      //                      )
      //                })
      //        }
      //        </tbody>
      // </table>
}