import {ProjectListScreen} from "./screens/project-list";
import {useAuth} from "./context/auth-context";
import styled from "@emotion/styled";
import {Row} from "./components/lib";
import { ReactComponent as SoftwareLogo } from "./assets/software-logo.svg";
import software from "./assets/software-logo.svg";
import {Button, Drawer, Dropdown, Menu} from "antd";
import {Navigate,Route,Routes} from 'react-router';
import {BrowserRouter as Router} from 'react-router-dom';
import {ProjectScreen} from "./screens/project";
import React, {useState} from "react";
import {ProjectModel} from "./screens/project-list/project-model";
import {ProjectPopover} from "./components/project-popover";
import {useTextShow} from "./screens/project-list/util";
import {Pin} from "./components/pin";

export const AuthenticatedApp = () => {

    // const value : any = undefined
      const [projectModelOpen,setProjectModelOpen] = useState(false);
      return <Container>
          {/*{value.name}*/}
          <Router>
            <PageHeader></PageHeader>
            <Main>
                {/*<ProjectListScreen></ProjectListScreen>*/}

                    <Routes>
                        <Route path={'/projects'} element={<ProjectListScreen setProjectModelOpen={setProjectModelOpen}/>}></Route>
                        <Route path={'/projects/:projectId/*'} element={<ProjectScreen/>}></Route>
                        <Navigate to={'/projects'}></Navigate>
                    </Routes>

            </Main>
          <ProjectModel></ProjectModel>
          </Router>
      </Container>
}
const PageHeader = ()=> {
      const {logout,user} = useAuth()
      const {state:flag} = useTextShow();
      return    <Header between={true}>
          <HeaderLeft gap={true}>
              {/*<img src={software} alt=""/>*/}
             <Button style={{padding:0}} type={'link'} onClick={() => {window.location.href=window.location.origin}}>
                 <SoftwareLogo width={"18rem"} color={"rgb(38, 132, 255)"} />
             </Button>
              <ProjectPopover></ProjectPopover>
              <span>用户</span>
              {/*<Pin checked={flag}></Pin>*/}
          </HeaderLeft>
          <HeaderRight>
              <Dropdown overlay={
                  <Menu>
                      <Menu.Item key={'logout'}>
                          <a  onClick={logout}>登出</a>
                      </Menu.Item>
                  </Menu>
              }>
                  <a  onClick={e => e.preventDefault()}>
                      hi, {user?.name}
                  </a>
              </Dropdown>
          </HeaderRight>
      </Header>
}
const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr;
  height: 100vh;
`;

const Header = styled(Row)`
    padding: 3.2rem;
    box-shadow: 0px 0px 5px 0 rgba(0,0,0,0.1);
`
//用上面替代
// const Header = styled.header`
//   display: flex;
//   flex-direction: row;
//   align-items: center;
//   justify-content: space-between;
// `

const HeaderLeft = styled(Row)`
   
`
// const HeaderLeft = styled.div`
//    display: flex;
//    align-items: center;
// `
const HeaderRight = styled.div``
const Main = styled.main`
  display: flex;
  overflow: hidden;
`